.navFixed {
  display: none;
}

.navRelative {
  position: relative;
  transition: 0.3;
}



.burgerHidden {
  opacity: 0;
}

.burgerNav {
  position: fixed;
  top: 2%;
  left: 95.5%;
  background-color: hsl(0, 0%, 25%);
  height: 4rem;
  width: 4rem;
  border-radius: 1rem;
  z-index: 200;
  opacity: 0;
  transition: opacity 0.2s ease-in;
}

.burgerNav.burgerFixed {
  opacity: 1;
  transition: opacity 0.3s ease-in;
}

#burger {
  font-size: 2rem;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: rgb(238, 236, 212);

}
.menuFlex {
  position: relative;
  top: -64px;
  left: 95.5%;
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
  background-color: hsl(0, 0%, 25%);
  width: 26.5rem;
  height: 4rem;
  border-radius: 1rem;
color: white;
z-index: -1;
}

.menuClose{
-webkit-transform: translateX(22%);
        transform: translateX(22%);
opacity: 0;
transition: opacity 0.3s ease-out, -webkit-transform 0.2s ease-out;
transition: transform 0.2s ease-out, opacity 0.3s ease-out;
transition: transform 0.2s ease-out, opacity 0.3s ease-out, -webkit-transform 0.2s ease-out;

}

.menuClose.menuOpen{
  -webkit-transform: translateX(-105%);
          transform: translateX(-105%);
  opacity: 1;
  transition: opacity 0.5s ease-in, -webkit-transform 0s ease-in;
  transition: transform 0s ease-in, opacity 0.5s ease-in;
  transition: transform 0s ease-in, opacity 0.5s ease-in, -webkit-transform 0s ease-in;
}

.menuFlex li {
  margin: auto 8px;
  padding: 5px;
  width: auto;
