.navFixed {
  display: none;
}

.navRelative {
  position: relative;
  transition: 0.3;
}



.burgerHidden {
  opacity: 0;
}

.burgerNav {
  position: fixed;
  top: 2%;
  left: 95.5%;
  background-color: hsl(0, 0%, 25%);
  height: 4rem;
  width: 4rem;
  border-radius: 1rem;
  z-index: 200;
  opacity: 0;
  transition: opacity 0.2s ease-in;
}

.burgerNav.burgerFixed {
  opacity: 1;
  transition: opacity 0.3s ease-in;
}

#burger {
  font-size: 2rem;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(238, 236, 212);

}
.menuFlex {
  position: relative;
  top: -64px;
  left: 95.5%;
  list-style-type: none;
  display: flex;
  background-color: hsl(0, 0%, 25%);
  width: 26.5rem;
  height: 4rem;
  border-radius: 1rem;
color: white;
z-index: -1;
}

.menuClose{
transform: translateX(22%);
opacity: 0;
transition: transform 0.2s ease-out, opacity 0.3s ease-out;

}

.menuClose.menuOpen{
  transform: translateX(-105%);
  opacity: 1;
  transition: transform 0s ease-in, opacity 0.5s ease-in;
}

.menuFlex li {
  margin: auto 8px;
  padding: 5px;
  width: auto;
;
}

.link {
  text-decoration: none;
  color: #000100;
}
.linkBrg{
  color: white;
  text-decoration: none;
}

.link:hover {
  text-decoration: none;
  cursor: pointer;
  transform:scale(1.5);
  color: #AED99A;
}

.linkBrg:hover{
  color: #AED99A;
  text-decoration: none;
  
}