section {
  height: 75vh;
  background-color: hsl(0, 0%, 70%);
}

.gridClass:nth-child(odd){
  display: flex;
  justify-content: flex-end;
  align-items: end;
  transform: translateY(-40%);
  opacity: 0;
  transition: transform 1s, opacity 1s ease-in;
}
.gridClass.slide:nth-child(odd){
  transform: translateY(0%);
  opacity: 1;
}
.gridClass:nth-child(even){
  transform: translateY(40%);
  opacity: 0;
  transition: transform 1s, opacity 1s ease-in;
}

.gridClass.slide:nth-child(even){
  transform: translateY(0%);
  opacity: 1;
}


.hideLeft{
  transform: translateX(-25%);
  opacity: 0;
  transition: transform 1s, opacity 1s ease-in;
}

.hideLeft.slideRight {
  transform: translateX(0%);
  opacity: 1;
}