.myImg {
  width: 80%;
  margin-left: 35px;
}

.hideLeft{
  transform: translateX(-25%);
  opacity: 0;
  transition: transform 1s, opacity 1s ease-in;
}

.hideLeft.slideRight {
  transform: translateX(0%);
  opacity: 1;
}

.hideRight{
  transform: translateX(25%);
  opacity: 0;
  transition: transform 1s, opacity 1s ease-in;
}

.hideRight.slideLeft {
  transform: translateX(0%);
  opacity: 1;
}