#kontaktDiv {
  clip-path: polygon(49% 21%, 100% 0, 100% 100%, 0 99%, 0 0);
  background-color: #AED99A;
  height: 100vh;
}

.kntTxt {
  position: relative;
  top: 30%;
  left: 46%;
}

#kontaktInfo {
  position: relative;
  top: 30%;
  margin-top: 70px;
}

#inputInfo {
  position: relative;
  top: 35%;
}

.inputBox{
  width: 400px;
}

.pTxt {
  font-size: 2rem;
}

#inputInfo {
  position: relative;
  top: 350px;
}

.btnSub{
  left: 120px;
}


.hideLeft{
  transform: translateX(-25%);
  opacity: 0;
  transition: transform 1s, opacity 1s ease-in;
}

.hideLeft.slideRight {
  transform: translateX(0%);
  opacity: 1;
}

.hideRight{
  transform: translateX(25%);
  opacity: 0;
  transition: transform 1s, opacity 1s ease-in;
}

.hideRight.slideLeft {
  transform: translateX(0%);
  opacity: 1;
}