:root {
  --lighterGreen: #AED99A;
  --lightGreen: #4F8137;
  --green: #25520F;
  --darkGreen: #0B2100;
  --mainBlack: #000100;
}

body {
  font-family: "lato" !important;
  background-color: hsl(0, 0%, 70%) !important;

}
/* Header */

header {
  clip-path: polygon(100% 0, 100% 65%, 27% 100%, 0 79%, 0 0);
  background-image: linear-gradient(to right, rgb(31, 66, 14) , rgb(188, 228, 170));
  height: 93vh;
  width: 100%;
  color: var(--mainBlack);
  
}
.icon-bcg {
  font-size: 50vh;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.15;
  z-index: -1;
}

header button {
position: absolute;
left: 45%;
top: 10px;
}
.centerCol {
  position: relative;
  top: 40%;
}

header div a {
  text-decoration: none;
  color: white;
}
header div a:hover {
  text-decoration: none;
  color: white;
}


/* Navbar */

#mainNav {
  width: 100%;
  height: 5vh;
  background-color:hsl(0, 0%, 70%);
}



main {
  height: 95vh;
 background-color: hsl(0, 0%, 70%);
  margin-top: 80px;
}